.star {
  display: flex;

  &__star {
    margin: 2px 6px 2px 2px;
    height: 12px;
    width: 12px;

    background-image: url("../images/star.svg");
    background-size: cover;
  }
}

.stars--1 .star__star:nth-child(-n+1),
.stars--2 .star__star:nth-child(-n+2),
.stars--3 .star__star:nth-child(-n+3),
.stars--4 .star__star:nth-child(-n+4),
.stars--5 .star__star:nth-child(-n+5) {
  background-image: url("../images/star-active.svg");
}
